import React from "react";
import ToursData from "./ToursData";
import ToursDetails from "./ToursDetails";
import "./Tours.css";

const Tours = () => {
  return (
    <div className="tours">
      <h3 className="toursheader">Tours</h3>
      {ToursData.map((item) => {
        return (
          <ToursDetails
            key={item.id}
            date={item.date}
            city={item.city}
            place={item.place}
          />
        );
      })}
    </div>
  );
};

export default Tours;
